import React, { Component } from 'react';

import * as WS from '../ws';

import Loader from '../gui_component/loader';

import { ReactComponent as SVGlogotb } from '../svg/logo_t_b.svg';

const errors_clean = {
	'email': '',
	'pw': '',
	'pwr': '',
	'recaptcha_token': ''
}

export default class GuiEnterMenuRegister extends Component {
	constructor(props) {
		super(props);

		this.email = React.createRef();
		this.pw = React.createRef();
		this.pwr = React.createRef();

		this.recaptcha_token = '';

		this.state = {
			loading: 0,
			errors: Object.create(errors_clean),
			btn_inactive: 1
		};
	}

	signup = (e) => {
		e.preventDefault();

		if (this.state.btn_inactive) {
			return;
		}

		this.setState({
			loading: 1,
			errors: Object.create(errors_clean)
		}, () => {

			const obj = JSON.stringify({
				'task': 'register_user',
				'data': {
					'email': this.email.current.value,
					'pw': this.pw.current.value,
					'pwr': this.pwr.current.value,
					'recaptcha_token': this.recaptcha_token
				}
			})

			window.current_ws_task = (data) => {

				let errors = this.state.errors;

				if (data.errors) {
					for (const key in data.errors) {
						errors[key] = data.errors[key];
					}
					this.setState({
						loading: 0,
						errors: errors
					})
				} else if (data.task === 'registration_confirmed') {
					this.props.history.push('/registration-confirmed');
				}

			}

			WS.send(obj);
		})
	}

	componentDidMount() {
		window.recaptcha_verify = (response) => {
			this.recaptcha_token = response;
		}
		window.recaptcha_onload = () => {

			window.grecaptcha.render('g-recaptcha', {
				'sitekey' : process.env.REACT_APP_RECAPTCHA_CLIENT_KEY,
				'callback' : window.recaptcha_verify,
				'theme' : 'clean'
			});

			this.setState({
				btn_inactive: 0
			})
		}
		let recaptcha_script = document.createElement('script');
		recaptcha_script.setAttribute('id', 'recaptcha_script');
		recaptcha_script.setAttribute('src', 'https://www.google.com/recaptcha/api.js?onload=recaptcha_onload&render=explicit');
		document.head.appendChild(recaptcha_script);
	}
	componentDidUnMount() {
		document.getElementById("recaptcha_script").remove();
	}

	render() {
		return (
			<div className="enter_menu" data-loading={this.state.loading}>
				<div className="register_menu">
					<div>
						<div>
							<Loader />
							<form>
								<div className="upper">
									<div className="logo"><SVGlogotb /></div>
									<strong className="blue_1">Nice to meet you.</strong>
									<strong className="gray_1">Enter your email address and password, and we create your account.</strong>
								</div>
								<div className="title_cont">
									<h6 className="gray_2">Email</h6>
								</div>
								<div className="input_cont">
									<div>
										<input type="text" ref={this.email} autoComplete="email" />
									</div>
									<div className="error_cont">{this.state.errors.email}</div>
								</div>

								<div className="title_cont">
									<h6 className="gray_2">Password</h6>
								</div>
								<div className="input_cont">
									<div>
										<input type="password" ref={this.pw} placeholder="strong password" autoComplete="on" />
									</div>
									<div className="error_cont">{this.state.errors.pw}</div>
								</div>
								<div className="input_cont">
									<div>
										<input type="password" ref={this.pwr} placeholder="repeat password" autoComplete="on" />
									</div>
									<div className="error_cont">{this.state.errors.pwr}</div>
								</div>

								<div className="input_cont">
									<div id="g-recaptcha" data-sitekey={process.env.REACT_APP_RECAPTCHA_CLIENT_KEY}></div>
									<div className="error_cont">{this.state.errors.recaptcha_token}</div>
								</div>

								<div className="button_cont tar_">
									<a className="btn btn_blue btn_sm shadow" data-inactive={this.state.btn_inactive} onClick={e => this.signup(e)}  href="/"><span>Sign Up</span></a>
								</div>
							</form>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
