import React from 'react';
import ReactDOM from 'react-dom';

import {
	Route,
	BrowserRouter as Router,
	Switch,
} from 'react-router-dom'

import * as WS from './ws';
import GuiEnterMenuLogin from './gui_enter_menu/login'
import GuiEnterMenuRegister from './gui_enter_menu/register'
import GuiEnterMenuRegisterConfirmed from './gui_enter_menu/register_confirmed'
import GuiEnterMenuForgotPassword from './gui_enter_menu/forgot_password'

import './css/import.css';

WS.connect2Server();

ReactDOM.render(
	<React.StrictMode>
		<div className="wrapper">
			<Router>
				<Switch>

					<Route path='/login' component={GuiEnterMenuLogin} />
					<Route path='/register' component={GuiEnterMenuRegister} />
					<Route path='/registration-confirmed' component={GuiEnterMenuRegisterConfirmed} />
					<Route path='/forgot-password' component={GuiEnterMenuForgotPassword} />

				</Switch>
			</Router>
		</div>
	</React.StrictMode>,
	document.getElementById('root')
);
