const WS = {
	// settings
	serverHost: window.location.hostname,
	serverPort: 9300,
	usernameMaxLength: 18,
	
	// internal variables
	socket: false,
	connected: false,
	
	// events
	connect2Server: function() {
		
		if (WS.isConnected()) {
			WS.disconnect();
			return;
		}

		console.log('connecting');
		WS.connect();
	},

	isConnected: function() {
		return WS.connected;
	},
	connect: function() {
		var protocol = 'ws';
		if (window.location.protocol === 'https:') {
			protocol = 'wss';
		}
		var Socket = new WebSocket(protocol+'://'+WS.serverHost+':'+WS.serverPort);
		WS.setSocketEvents(Socket);
		WS.socket = Socket;
	},
	disconnect: function() {
		WS.socket.send('QUIT');
		WS.socket.close();
	},
	
	// socket events
	setSocketEvents: function(Socket) {
		Socket.onopen = function() {
			WS.connected = true;
			console.log('connected');
			document.querySelector('body').removeAttribute('data-loading');

			WS.socket.send('init-user');
		}
		
		Socket.onmessage = function(Message) {
			var data = Message.data;
			data = JSON.parse(data);
			// console.log(data);

			window.current_ws_task(data);
		}
		
		Socket.onclose = function() {
			if (WS.connected) {
				console.log('websocket disconnected');
			}
			else {
				console.log('failed to connect');
			}
			
			WS.connected = false;

		}
	},


}

export function connect2Server() {
	WS.connect2Server();
}
export function send(message) {
	WS.socket.send(message);
}

export default WS => '';